@import '../../assets/css/variable.less';

.card-list {
  cursor: pointer;
  a {
    text-decoration: none;
  }
  .card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    border-bottom: 1px solid @divider_color;
    padding: 0 24px;

    .pool-network-type {
      display: flex;
      justify-content: space-between;
      align-items: center;
      
      .pool-type-network-text {
        margin-left: 12px;
        font-weight: 600;

        .pool-type-text {
          color: #FFFFFF;
        }

        .pool-network-text {
          font-size: 14px;
        }
      }

      .network-logo {
        img {
          width: 28px;
          height: 28px;
        }
      }
    }

    .base-token-logo {
      height: 40px;

      .base-token-box-bg {
        width: 48px;
        height: 48px;
        display: inline-block;
        border-radius: 50%;
        background-color: #0b182d;
        position: relative;
        .basa-token-bg {
          width: 40px;
          height: 40px;
          background-color: rgba(255, 255, 255, 0.1);
          border-radius: 50%;
          display: flex;
          align-items: center;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        
      }
      .base-token-box-bg:nth-child(n) {
        margin-left: -12px;
      }

      .base-token-box-bg:first-child {
        margin-left: 0;
      }
      .lp-base-token-bg{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 24px;
        .lp-con{
          margin: 0 5px;
        }
        .basa-token-bg{
          width: 40px;
          height: 40px;
          background-color: rgba(255, 255, 255, 0.1);
          border-radius: 50%;
          display: flex;
          align-items: center;
        }
      }
      .WBNB,.WMATIC,.amUSDC,.BUSD,.USDT,.CAKE,.DERI,.TBUSD,.TDERI{
        background-size: 20px 20px;
      }
      .DERI,.TDERI{
        background-image: url('../../assets/img/logo.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
      .WBNB {
        background-image: url('../../assets/img/wbnb.svg');
        background-repeat: no-repeat;
        background-position: center;
      }

      .WMATIC {
        background-image: url('../../assets/img/matic.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
      .amUSDC{
        background-image: url('../../assets/img/amusdc.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
      .BUSD,.TBUSD{
        background-image: url('../../assets/img/busd.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
      .USDT{
        background-image: url('../../assets/img/usdt.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
      .CAKE {
        background-image: url('./img/Pancake.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
    }


  }

  .card-content {
    height: 404px;
    padding: 48px 24px 40px 24px;

    .max-apy-title,
    .total-liquidity-title {
      font-size: 14px;
    }

    .max-apy-num,
    .total-liquidity-num {
      font-size: 36px;
      color: @whiteFontColor;
      margin-top: 5px;
      height: 45px;
    }

    .total-pool-liquidity {
      margin-top: 24px;
    }

    .your-deri-epoch {
      font-size: 12px;
      height: 20px;
      margin-top: 48px;

      div {
        width: 80px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px;
        border: 1px solid rgba(224, 236, 255, 0.5);
        border-radius: 100px;
      }
    }

    .stake-button {
      margin-top: 24px;

      .button {
        width: 332px;
        height: 56px;
        border: 0;
        background: rgba(255, 255, 255, 0.1);
        color: #FFFFFF;
        border-radius: 4px;
        font-size: 18px;
        cursor: pointer;
      }

    }
  }
}
.card-list:hover{
  background: linear-gradient(135deg,rgba(97,122,255,0.1) 0%,rgba(223,229,255,0) 50.52%,rgba(85,91,237,0.05) 100%);
}
.card-list:hover .stake-button button{
  background-color: rgba(85, 119, 253, 0.1);
}

.card-list:first-child {
  .card-header {
    .base-token-box-bg {
      background-color: #111e34;
    }
  }
}