@import '../../assets/css/variable.less';
@border: 1px solid @divider_color;

.filter-modal {
  width: 380px;
  height: 592px;
  background: #203B60;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;

  .close-filter-modal {
    height: 107px;
    text-align: right;
    padding-right: 24px;
    line-height: 107px;
  }

  .staked-only{
    width: 380px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 24px;
    padding-right: 24px;
    margin-bottom: 24px;
    color: #FFFFFF;
  }

  .select-pool-type-network {
    border-bottom: 1px solid @divider_color;

    .pool-type {
      width: 380px;
      height: 184px;
    }

    .type-cols {
      width: 380px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-left: 24px;
      padding-right: 24px;

      .type-title {
        font-size: 14px;
        color: #FFFFFF;
      }
    }

    .network {
      margin-top: 8px;
      margin-bottom: 16px;
    }
  }

  .select-footer {
    color: @second_color;
    font-size: 18px;
    height: 41px;
    line-height: 41px;

    .clear-all-filter {
      text-align: center;
      padding-top: 15px;
    }
  }
}